.tsparticles-canvas-el {
  width: 0;
  height: 0;
}

.App {
  text-align: center;
  /* background-color: rgb(0, 55, 87); */
  /* background-image: "src/images/Mountain2.jpg"; */
}

#particles-js {
  position : absolute;
  width:100%;
  height:100%;
  z-index:-1;
}

.card {
  border: none;
  /* width: 150%; */
}

.about-me {
  padding-right: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.carousel-item.active,
.carousel-item-next,
.carousel-item-prev {
  display: block;
}

/* Footer */

.footer {
  background: #e7e7e7;
  color: rgb(0, 55, 87);
}
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
}

